* {
  box-sizing: border-box;
}


body {
  margin: 0;
  background-color: rgb(0,31,43);

}

.App {
  background-color: rgb(0,31,43);
}

.nav {
  background-color: rgb(0,65,90);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 0 1rem;
  gap: 2rem;
}

.nav site-title {
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: .25rem;
  margin: 0.1rem;
}

.nav li {
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 8px;
}

.nav li.active {
  background-color: rgb(0,31,43);
}

.nav li:hover {
  background-color: rgb(1, 49, 68);
}
/*   */
.Intro {
  font-family: 'Roboto Slab';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.IntroTitle {
  font-size: 50px;
  font-weight: bold;
  color: #4e545f;
  margin-top: 30px;
  margin-bottom: 10px;
}

.IntroParagraph {
  font-size: large;
  color: #282c34;
  margin: 10px;
}

.pages {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contactPage {
  background-color: rgb(0,65,90);
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin: 20px;
  border-radius: 10px;
  width: 450px;
  justify-content: center;
  align-items: center;
}

.socialIcons {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}

.iconLink {
  display: flex;
  color: #ffffff;
  margin: 10px;
  font-size: 40px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
